import React, { useState, useEffect, useRef } from 'react';

function hasGetUserMedia() {
  return !!(
    (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) ||
    navigator.webkitGetUserMedia ||
    navigator.mozGetUserMedia ||
    navigator.msGetUserMedia
  );
}

const Camera = ({children}) => {
	const [cameraList, setCameraList] = useState([]);
	const [selected, setSelected] = useState(1);
	const [stream, setStream] = useState(null);
	const [error, setError] = useState(null);
	const container = useRef(null);


	useEffect(() => {
		const getCameraList = async () => {
			if('mediaDevices' in navigator && 'enumerateDevices' in navigator.mediaDevices) {
				const list = await navigator.mediaDevices.enumerateDevices();
				let camList = [];
				list.forEach(dev => {
					if(dev.kind === 'videoinput')
						camList.push(dev);
				});
				setCameraList(camList);
			}
		};
		getCameraList();
	// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	useEffect(() => {
		const getCameraPermission = async () => {
			setError(null);
			const selectedCamera = !!cameraList.length ? cameraList[selected]:null;
			//navigator.mediaDevices.enumerateDevices().then(d => d.forEach(b=>alert(b.kind)))
			if(hasGetUserMedia() && !!selectedCamera){
				navigator.getUserMedia =
				navigator.mediaDevices.getUserMedia ||
				navigator.webkitGetUserMedia ||
				navigator.mozGetUserMedia ||
				navigator.msGetUserMedia;
				try {
					const stream = await navigator.mediaDevices.getUserMedia({
						video: true,
						deviceId: {
							exact: selectedCamera.deviceId
						}
					});
					setStream(stream);
					container.current.srcObject = stream;
				} catch (e) {
					return setError(new Error('Please allow us to use the camera.'))
				}
			} else {
				return setError(new Error('No camera device was found!'));
			};
		};
		getCameraPermission();
	}, []);

	return (
		<>
			<select onChange={(e) => {setSelected(e.target.value)}}>
				{cameraList.map((cam, i) => (
					<option key={cam.deviceId} value={i}>{cam.label}</option>
				))}
			</select>
			<video ref={container} autoPlay></video>
			{error && <span>{error.message}</span>}
		</>
			//children(stream, error)
	);
}

export default Camera;
